<template>
        <publicMaskPopup ref="tipsPopupRef" isCenter :isClickMaskHide="false">
                <template #slot_mask>
                        <div class="detailsPopupMain">
                                <img class="close" alt="" src="@/assets/images/icons/close.png"
                                        @click="tipsPopupRef.maskHide" />
                                <div class="dpSwiper">
                                        <div class="swiperContainer">
                                                <Swiper ref="swiperRef" space-between="20" :modules="[SwiperNavigation]"
                                                        :auto-height="true"
                                                        :navigation="{
                                                                nextEl: '.box.next',
                                                                prevEl: '.box.prev',
                                                        }" @swiper="onSwiper" @slideChange="onChange">
                                                        <SwiperSlide>
                                                                <div class="swiperItem">
                                                                        <p class="title">{{ t("common.MFTChallenge") }}
                                                                        </p>
                                                                        <p class="subTitle">{{
                                                                                t("Home.challenge.text18") }}</p>
                                                                        <div class="picWrap">
                                                                                <p class="name">MFT Challenge- step1</p>
                                                                                <img class="pic" alt=""
                                                                                        src="@/assets/images/page/detail-pic1.png" />
                                                                        </div>
                                                                </div>
                                                        </SwiperSlide>
                                                        <SwiperSlide>
                                                                <div class="swiperItem">
                                                                        <p class="title">{{ t("common.verification") }}
                                                                        </p>
                                                                        <p class="subTitle">{{
                                                                                t("Home.challenge.text19") }}</p>
                                                                        <div class="picWrap">
                                                                                <p class="name">MFT Challenge- step2</p>
                                                                                <img class="pic" alt=""
                                                                                        src="@/assets/images/page/detail-pic2.png" />
                                                                        </div>
                                                                </div>
                                                        </SwiperSlide>
                                                        <SwiperSlide>
                                                                <div class="swiperItem">
                                                                        <p class="title">{{ t("common.trader") }}</p>
                                                                        <p class="subTitle">{{
                                                                                t("Home.challenge.text20") }}</p>
                                                                        <div class="picWrap">
                                                                                <p class="name">MFT Challenge- step3</p>
                                                                                <img class="pic" alt=""
                                                                                        src="@/assets/images/page/detail-pic3.png" />
                                                                        </div>
                                                                </div>
                                                        </SwiperSlide>
                                                        <SwiperSlide>
                                                                <div class="swiperItem">
                                                                        <p class="title">{{ t("common.proTrader") }}</p>
                                                                        <p class="subTitle">{{
                                                                                t("Home.challenge.text21") }}</p>
                                                                        <div class="descWrap">
                                                                                <p class="wrapTitle linearColor">{{
                                                                                        t(HOW_TO_SUCCESS[2].stepList[0].subTitle)
                                                                                        }}</p>
                                                                                <p>{{ t(HOW_TO_SUCCESS[2].stepList[0].txt)
                                                                                        }}</p>
                                                                                <p class="wrapTitle linearColor mt">{{
                                                                                        t(HOW_TO_SUCCESS[2].listTitle)
                                                                                        }}</p>
                                                                                <ul class="list">
                                                                                        <li v-for="(item, idx) in HOW_TO_SUCCESS[2].list"
                                                                                                :key="idx">
                                                                                                <img class="icon" alt=""
                                                                                                        :src="item.listIcon" />
                                                                                                {{ t(item.content) }}
                                                                                        </li>
                                                                                </ul>
                                                                                <p class="linearColor">
                                                                                        {{ t("Home.challenge.text22") }}
                                                                                </p>
                                                                        </div>
                                                                </div>
                                                        </SwiperSlide>
                                                </Swiper>
                                        </div>
                                </div>
                                <div class="stepWrap">
                                        <div class="boxOuter">
                                                <p class="box prev" v-show="keyValue !== 0"><i
                                                                class="iconfont icon-back"></i></p>
                                        </div>
                                        <p class="step">Step {{ keyValue + 1 }}</p>
                                        <div class="boxOuter">
                                                <p class="box next" v-show="keyValue !== 3"><i
                                                                class="iconfont icon-back"></i></p>
                                        </div>
                                </div>
                                <nuxt-link-locale class="btn" to="/user">{{ t("User.list.btn3") }}</nuxt-link-locale>
                        </div>
                </template>
        </publicMaskPopup>
</template>
<script setup>
import { HOW_TO_SUCCESS } from "@/data/challengesData"
const { t } = useI18n()
const tipsPopupRef = ref()
const keyValue = ref(0)
const swiperRef = ref(null)
const keepSwiper = ref(null)
const onSwiper = (swiper) => {
        swiperRef.value = swiper;
};
const onChange = (e) => {
        keyValue.value = e.activeIndex
}
const shower = (i) => {
        keyValue.value = i
        tipsPopupRef.value.maskShow()
        nextTick(() => {
                keepSwiper.value.slideTo(i)
        })
}
const maskClose = () => {
        tipsPopupRef.value.maskHide()
}
defineExpose({ shower, maskClose });
onMounted(() => {
        keepSwiper.value = swiperRef.value
})
</script>
<style scoped lang="scss">
.detailsPopupMain {
        width: 900px;
        height: auto;
        background-color: #000000;
        border-radius: 24px;
        padding: 40px;
        box-sizing: border-box;
        color: #FFFFFF;

        .close {
                width: 22px;
                height: 22px;
                position: absolute;
                top: 20px;
                right: 20px;
                cursor: pointer;
                z-index: 99;
        }

        .title {
                @include setfont(28px, #FFFFFF, 700);
                line-height: 42px;
                text-align: center;
                white-space: pre-line;
        }

        .dpSwiper {
                width: 100%;
                height: auto;
                position: relative;

                .swiperContainer,
                .swiperItem {
                        width: 100%;
                        height: 100%;
                        position: relative;
                }
        }

        .subTitle {
                @include setfont(14px, #FFFFFF, 700);
                line-height: 21px;
                margin-top: 20px;
                white-space: pre-line;
        }

        .picWrap {
                margin-top: 20px;
                position: relative;

                .name {
                        @include setfont(14px, #FFFFFF, 700);
                        line-height: 18px;
                        position: absolute;
                        top: 40px;
                        left: 30px;
                        z-index: 22;
                }

                .pic {
                        width: 100%;
                        height: auto;
                }
        }

        .descWrap {
                margin-top: 40px;
                width: 100%;
                box-sizing: border-box;
                padding: 40px 30px 70px;
                border-radius: 24px;
                background-color: #161616;
                color: #FFFFFF;
                font-size: 12px;
                line-height: 18px;

                .mt {
                        margin-top: 30px;
                }

                .linearColor {
                        color: transparent;
                        background: linear-gradient(90deg, #FFE456 0%, #FFFFFF 47.33%);
                        background-clip: text;
                }

                .wrapTitle {
                        font-size: 16px;
                        line-height: 24px;
                        font-weight: 700;
                        margin-bottom: 10px;
                }

                .list {
                        &>li {
                                margin-bottom: 16px;
                                @include dfac;

                                .icon {
                                        width: 12px;
                                        height: 12px;
                                        margin-right: 4px;
                                }
                        }
                }
        }

        .stepWrap {
                margin-top: 40px;
                @include dfjcac;

                .boxOuter {
                        width: 28px;
                        height: 28px;
                }

                .box {
                        width: 100%;
                        height: 100%;
                        border-radius: 4px;
                        background-color: #29282C;
                        @include dfjcac;
                        cursor: pointer;

                        .icon-back {
                                font-size: 14px;
                        }

                        &.next .icon-back {
                                transform: rotate(180deg);
                        }
                }

                .step {
                        margin: 0 40px;
                        width: 64px;
                        height: 28px;
                        box-sizing: border-box;
                        border-radius: 4px;
                        @include dfjcac;
                        background-color: transparent;
                        border: 1px solid #FFFFFF;
                        font-size: 12px;
                        font-weight: 700;
                }
        }

        .btn {
                @extend %bg_btn;
                width: 340px;
                height: 42px;
                border-radius: 5em;
                margin: 40px auto 0;
                @include setfont(16px, #FFFFFF, 700);
                cursor: pointer;
                @include dfjcac;
        }
}

@media screen and (max-width: 1024px) {
        .detailsPopupMain {
                width: 87.2vw;
                border-radius: 6.4vw;
                padding: 4.2667vw 4.2667vw 10.6667vw;

                .close {
                        width: 5.3333vw;
                        height: 5.3333vw;
                        top: 5.3333vw;
                        right: 5.3333vw;
                }

                .title {
                        font-size: 4.8vw;
                        line-height: 7.2vw;
                }

                .subTitle {
                        margin-top: 5.3333vw;
                        font-size: 3.2vw;
                        line-height: 4.8vw;
                }

                .picWrap {
                        margin-top: 4vw;

                        .name {
                                top: 4vw;
                                left: 3.2vw;
                                font-size: 2.6667vw;
                                line-height: 3.2vw;
                        }
                }

                .descWrap {
                        margin-top: 5.3333vw;
                        padding: 3.2vw 2.6667vw 5.3333vw;
                        border-radius: 1.6vw;
                        font-size: 12px;
                        line-height: 18px;

                        .mt {
                                margin-top: 5.3333vw;
                        }

                        .wrapTitle {
                                font-size: 3.7333vw;
                                line-height: 5.6vw;
                                margin-bottom: 3.7333vw;
                        }

                        .list {
                                &>li {
                                        margin-bottom: 4.2667vw;

                                        .icon {
                                                width: 12px;
                                                height: 12px;
                                                margin-right: 4px;
                                        }
                                }
                        }
                }

                .stepWrap {
                        margin-top: 5.3333vw;

                        .boxOuter {
                                width: 7.4667vw;
                                height: 7.4667vw;
                        }

                        .box {
                                border-radius: 1.0667vw;

                                .icon-back {
                                        font-size: 3.7333vw;
                                }
                        }

                        .step {
                                margin: 0 10.6667vw;
                                width: 17.0667vw;
                                height: 7.4667vw;
                                border-radius: 1.0667vw;
                                font-size: 3.2vw;
                        }
                }

                .btn {
                        width: 100%;
                        height: 11.2vw;
                        margin: 5.3333vw auto 0;
                        font-size: 4.2667vw;
                }
        }
}
</style>